/* Header */
.header-wrapper {
    width: 1023px;
    margin: 0 auto;
}

.header h1 {
    margin: 33px 112px 36px 0;
    display: inline-block;
    font: 39px 'Futura Bk BT', 'Trebuchet MS', Arial, sans-serif;
    font-weight: normal;
}

.header h1 a {
    text-decoration: none;
    color: #6FDA44;
    font-family: 'Lobster', cursive;
}

.header h1 span {
    text-transform: uppercase;
    font: 35px 'Futura Md BT', 'Trebuchet MS', Arial, sans-serif;
    font-weight: bold;
    color: #4B4B4B;
    margin-left: 5px;
}

.header ul {
    display: inline-block;
}

.header ul li {
    display: inline-block;
    text-transform: uppercase;
}

.menu a {
    padding: 66px 16px 38px 19px;
    text-decoration: none;
    color: #adadad;
    font: 600 14px 'Open Sans', sans-serif;
}

.menu a:hover {
    color: #008329;
    border-bottom: 4px solid #008329;
}