/* Contacts */
.contacts {
    font: 600 14px 'Open Sans', sans-serif;
    color: rgba(0, 0, 0, 0.5);
    position: relative;
}

.image-wrapper {
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    height: 345px;
}

.image-wrapper img {
    width: 100%;
    height: 100%;
}

.contacts-wrapper {
    width: 1023px;
    margin: 0 auto;
    padding-bottom: 26px;
}

.contact-map {
    float: right;
}

.contacts-wrapper p {
    margin: 0;
}

.contacts-address {
    position: relative;
    padding: 40px 0 15px 64px;
    line-height: 2.2;
}

.contacts-address img {
    position: absolute;
    top: 42px;
    left: 7px;
}

.contacts-phone {
    position: relative;
    padding: 0 0 15px 63px;
}

.contacts-phone img {
    position: absolute;
    top: -2px;
    left: 6px;
}

.contacts-email {
    position: relative;
    padding: 0 0 38px 64px;
}

.contacts-email img {
    position: absolute;
    top: 6px;
    left: 4px;
}

.socials {
    margin: 0;
    padding: 0 0 0 4px;
}

.social {
    display: inline-block;
    padding-right: 17px;
}