/* Portfolio */
.portfolio-wrapper {
    width: 1023px;
    margin: 0 auto;
}

.portfolio-header {
    padding-top: 60px;
    margin: 0 0 75px;
    text-transform: uppercase;
    text-align: center;
    color: #41525c;
    font: bold 30px 'Trebuchet MS', Arial, sans-serif;
}

.portfolio-block-wrapper {
    padding-bottom: 50px;
    white-space: nowrap;
    text-align: center;
}

.portfolio-block-left,
.portfolio-block-center,
.portfolio-block-right {
    display: inline-block;
    white-space: normal;
    vertical-align: top;
}

.portfolio-block-left {
    width: 33.3%;
}

.portfolio-block-center {
    width: 33.3%;
}

.portfolio-block-right {
    width: 33.3%;
}

.portfolio-block-header {
    margin: 18px 0 16px;
    text-transform: uppercase;
    color: #3070a8;
    font: 30px 'Futura Bk BT', 'Trebuchet MS', Arial, sans-serif;
}

.portfolio-block-paragraph {
    color: #a7a7a7;
    font: 400 14px/1.8 'Open Sans', sans-serif;
    width: 325px;
    margin: 0;
    word-spacing: 3px
}