/* Pricing */
.pricing {
    width: 848px;
    margin: 0 auto;
}

.pricing h1 {
    text-transform: uppercase;
    text-align: center;
    font: 30px 'Trebuchet MS', Arial, sans-serif;
    padding: 67px 0 58px 0;
    margin: 0;
}

.containers {
    display: flex;
    padding-bottom: 70px;
}

.blue-container {
    width: 290px;
    border: 1px solid #e8eaec;
    border-radius: 5px;
    margin-top: 10px;
    margin-bottom: 15px;
}

.best-value {
    position: relative;
    width: 290px;
    border: 4px solid #ee8129;
    border-radius: 5px;
    box-shadow: 0 0 5px 0 grey;
    z-index: 1;
}

.standard {
    margin-right: -5px;
}

.ultimate {
    margin-left: -5px;
}

.container-header {
    color: #fff;
}

.standard .container-header {
    background-color: #37c2d3;
    height: 63px;
    border-radius: 5px 5px 0 0;
}

.best-value .container-header {
    background-color: #ee8129;
    height: 73px;
}

.ultimate .container-header {
    background-color: #1cb79a;
    height: 63px;
    border-radius: 5px 5px 0 0;
}

.container-header h2 {
    font: bold 24px 'Futura Md BT', 'Trebuchet MS', Arial, sans-serif;
    padding: 23px 0 0 83px;
    margin: 0;
    text-transform: uppercase;
}

.price {
    text-align: center;
}

.price h2 {
    font: 24px 'Futura Bk BT', 'Trebuchet MS', Arial, sans-serif;
    margin-bottom: 12px;
    text-transform: uppercase;
}

div.price p {
    margin: 0 0 13px 0;
    color: #35495a;
}

.currency {
    font: bold 20px 'Futura Md BT', 'Trebuchet MS', Arial, sans-serif;
    vertical-align: 44px;
}

.value {
    font: bold 72px 'Futura Md BT', 'Trebuchet MS', Arial, sans-serif;
}

.month {
    font: 18px 'Futura Bk BT', 'Trebuchet MS', Arial, sans-serif;
    vertical-align: -8px;
}

.price ul {
    margin: 0 15px 17px 15px;
    width: 260px;
    padding: 0;
}

.price li {
    list-style-type: none;
    padding: 11px 0 6px 0;
    font: bold 15px Calibri, Candara, Segoe, 'Segoe UI', Optima, Arial, sans-serif;
    color: #888c94;
    text-align: left;
    margin-bottom: 1px;
}

.price li:before {
    content: '';
    width: 14px;
    height: 15px;
    float: left;
    margin: 0 16px 0 32px;
    background: url('../images/check.png') 0 0;

}

.price li:nth-child(even) {
    background-color: #f5f5f6;
}

.price li:nth-child(odd) {
    background-color: #edf5fd;
}

.price input.orange {
    background-color: #ee8129;
    color: #fff;
    font: bold 14px 'Open Sans', sans-serif;
    width: 250px;
    padding: 10px 0;
    margin-bottom: 32px;
    border: 1px solid #ee8129;
    border-radius: 4px;
    box-shadow: 0 3px 0 0 #b3560b;
    text-transform: uppercase;
}

.price input.blue {
    background-color: #3371a6;
    color: #fff;
    font: bold 14px 'Open Sans', sans-serif;
    width: 250px;
    padding: 10px 0;
    border: 1px solid #3371a6;
    border-radius: 4px;
    box-shadow: 0 3px 0 0 #16436a;
    text-transform: uppercase;
}

.price input:hover {
    box-shadow: none;
    cursor: pointer;
}