/* Services */
.services {
    background: url('../images/services-bg.png') no-repeat;
    background-size: cover;
}

.services-wrapper {
    width: 1023px;
    margin: 0 auto;
    padding: 0 0 57px 0;
}

.services-header {
    font: 26px/1.7 'Futura Bk BT', 'Trebuchet MS', Arial, sans-serif;
    color: #ee8129;
    text-transform: uppercase;
    margin: 0;
    padding: 79px 0 11px;
    width: 685px;
}

.services-subhead {
    font: 400 italic 19px 'Open Sans', sans-serif;
    color: rgba(255,255,255,0.9);
    margin: 0 0 33px;
    width: 685px;
}

.services ul {
    margin: 0;
    width:610px;
    list-style-type: none;
}

.services ul li {
    font: 400 14px/2.0 'Open Sans', sans-serif;
    word-spacing: 2px;
    margin-bottom: 20px;
    color: rgba(255,255,255,0.5);
}

.services ul li:hover {
    color: #3070a8;
}

.services ul li::before {
    content: '';
    float: left;
    margin: 10px 0 0 -20px;
    border: 4px solid rgba(255,255,255,0.5);
    border-radius: 10px;
}

.services ul li:hover:before {
    border-color: #3070a8;
}