/* Testimonials */
.testimonials {
    font-family: 'Open Sans', sans-serif;
    background: url('../images/testimonials-background.png') no-repeat;
    background-size: cover;
}

.testimonials-wrapper {
    width: 1023px;
    margin: 0 auto;
}

.testimonials h1 {
    font: 39px 'Futura Md BT', 'Trebuchet MS', Arial, sans-serif;
    text-transform: uppercase;
    color: #3170a8;
    padding: 47px 0 44px 0;
    margin: 0;
    text-align: center;
    letter-spacing: 1px;
}

.testimonials h1 span {
    color: #ee8129;
    font-family: 'Futura Bk BT', 'Trebuchet MS', Arial, sans-serif;
    font-weight: normal;
}

.customers {
    padding-bottom: 59px;
    overflow: hidden;
}

.customer-left,
.customer-right {
    float: left;
    height: 98px;
    overflow: hidden;
    margin-bottom: 20px;
    position: relative;
}

.customer-left {
    width: 48%;
    margin-right: 2%;
}

.customer-right {
    width: 48%;
    margin-left: 2%;
}

.customer-left img {
    position: absolute;
    top: 0;
    left: 3px;
}

.customer-right img {
    position: absolute;
    top: 0;
    left: 3px;
}

.comment {
    display: inline-block;
    background-color: #f7f7f7;
    width: 74%;
    vertical-align: top;
    margin-left: 127px;
}

.comment::before {
    content: '';
    float: left;
    margin: 35px 0 0 -26px;
    border-top: 13px solid #15191f;
    border-bottom: 13px solid #15191f;
    border-right: 13px solid #f7f7f7;
    border-left: 13px solid #15191f;
}

.testimonials-header {
    padding: 8px 15px 9px 15px;
}

.testimonials-header h2 {
    display: inline-block;
    font-size: 13px;
    font-style: italic;
    color: #3070a8;
    margin: 0;
}

.testimonials-header h2:nth-child(2) {
    float: right;
    margin: 4px 7px 0 0;
}

.name::before {
    content: '- ';
}

.comment p {
    font-size: 13px;
    color: #666;
    text-align: left;
    padding: 6px 0 18px;
    margin: 0 15px;
    border-top: 1px dotted #666;
    word-spacing: 2px;
}