/* Footer */
.footer {
    background-color: #666;
    font: 400 14px 'Open Sans', sans-serif;
    color: #fff;
    text-align: center;
}

.footer-wrapper {
    width: 1023px;
    margin: 0 auto;
}

.footer p {
    margin: 0;
}

.footer-company {
    padding: 25px 0 11px;
}

.footer-company span {
    font-weight: 600;
    color: #6FDA44;
}

.footer-design {
    padding-bottom: 30px;
}

.footer-design span {
    font-weight: 600;
    color: #000;
}