/* Banner */
.banner {
    background: url('../images/banner.jpg') no-repeat;
    background-size: cover;
    text-align: center;
    color: #fff;
}
.banner-wrapper {
    width: 1023px;
    margin: 0 auto;
}

.banner h1 {
    width: 880px;
    padding: 135px 0 19px;
    margin: 0 0 0 65px;
    font: 48px 'Futura Md BT', 'Trebuchet MS', Arial, sans-serif;
    text-transform: uppercase;
    font-weight: bold;
}

.banner h1 span {
    text-transform: none;
    word-spacing: 15px;
    font: 32px 'Futura Bk BT', 'Trebuchet MS', Arial, sans-serif;
}

.banner h2 {
    width: 585px;
    margin: 0 0 47px 213px;
    font: italic 20px Calibri, Candara, Segoe, 'Segoe UI', Optima, Arial, sans-serif;
}

.banner input {
    padding: 17px 64px;
    border: 3px solid #fff;
    margin: 0 9px 107px 0;
    background-color: transparent;
    color: #fff;
    text-transform: uppercase;
    font: 600 16px 'Open Sans', sans-serif;
    cursor: pointer;
}

.banner input:hover {
    color: #008329;
    background-color: #fff;
}